$header-color:#3085b0;
$question-color:#d2d2d2;
$question-color-hover:#c2c2c2;
$text-color:#333;
$question-margin:0.8em;
$success-color:green;
$danger-color:red;

#poll_container{
    padding:0.8em;
    border:1px solid #ddd;
    width:100%;
}

.row{
    margin:0;

    .poll_header{
        background-color:$header-color;
        h4{
            margin:0;
            padding-top:0.6em!important;
            padding-bottom:0.6em!important;
        }
    }

    h4{
        color:#fff;
        text-align:left;
        letter-spacing: 2px;
        line-height: 1.4em;
        font-weight: 550;
    }

}

.poll_answers{
    background-color:$question-color;
    margin-top:$question-margin;

    input{
        text-align:center;
    }

    h4{
    color:$text-color;
    margin:0;
    padding-top:0.2em;
    padding-bottom:0.2em;
    }

    &:hover {
        background-color:$question-color-hover;
        cursor:pointer;
    }

}

.poll_buttons{
    margin-top:$question-margin;
    button{
        background-color:$header-color;
        border-radius:0;
        color:#fff;
        font-size:1.2em;
        &:hover{
            background-color:$header-color;
            color:#fff;
        }
        &:focus{
            background-color:$header-color;
            color:#fff;
        }
        &:target{
            background-color:$header-color;
            color:#fff;
        }
    }
    .col-sm-6{
        padding-right:2px;
        padding-left:2px;
    }
}

.progress{
    .progress-bar{
        text-align:right;
        min-width:2em;
    }
    span{
        margin-right:5px;
    }
}

#poll-message{
        margin-bottom:0!important;
        margin-top:$question-margin;
        padding:8px;
}
